/**
 * Demonstrates SSR streaming (server config must enable streaming).
 */

export async function getServerSideProps() {
  return { chunks: ["Hello", " ", "from", " ", "streaming", "!"] };
}

export default function StreamPage(props: { chunks: string[] }) {
  return (
    <main style={{ fontFamily: "sans-serif", padding: 24 }}>
      <h1>Streaming SSR</h1>
      <p>{props.chunks.join("")}</p>
    </main>
  );
}


